<template id='backToTop'>
    <div>
      <button class="btn btn-primary upButton" :scroll="handleScroll"
       v-show="buttonVisible" @click="backToTop">▲</button>
    </div>
</template>

<script>
export default {
  name: 'BackToTop',
  data() {
    return {
      buttonVisible: false,
    };
  },
  methods: {
    backToTop() {
      window.scrollTo(0, 0);
    },
    handleScroll() {
      if (window.scrollY > 300) {
        this.buttonVisible = true;
      } else {
        this.buttonVisible = false;
      }
    },
  },
  created() {
    window.addEventListener('scroll', this.handleScroll);
  },
};
</script>

<style scoped>
.upButton {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 99999;
  cursor: pointer;
  padding: 15px 20px;
}
</style>
