  mounted() {
    fetch(`${SERVEURL}${this.$route.params.id}`)
      .then((response) => response.json())
      .then((result) => {
        const sezonFilter = result.find((x) => Object.prototype.hasOwnProperty.call(x, 'season'));
        if (sezonFilter !== undefined) {
          this.sezoni = sezonFilter.season;
        }
        const matches = result.find((x) => Object.prototype.hasOwnProperty.call(x, 'matches'));
        if (matches !== undefined) {
          this.ndeshjet = matches.matches;
        }
        const stand = result.find((x) => Object.prototype.hasOwnProperty.call(x, 'standings'));
        if (stand !== undefined) {
          this.renditjaTot = stand.standings.filter((x) => x.type === 'TOTAL');
        }
        this.loading = false;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    albName(ndeshje, origjinal) {
      const albanianName = this.compAlbNames.find((y) => y.id === ndeshje);
      if (albanianName) {
        return albanianName.albName;
      }
      return origjinal;
    },
    perditeso(TYPE) {
      if (TYPE === 'matches') {
        this.button1 = true;
      }
      if (TYPE === 'standings') {
        this.button2 = true;
      }
      fetch(`${UPDATEURL}${this.$route.params.id}/${TYPE}`)
        .then((response) => response.json())
        .then((res) => {
          if (Object.prototype.hasOwnProperty.call(res, 'matches')) {
            this.ndeshjet = res.matches;
          }
          if (Object.prototype.hasOwnProperty.call(res, 'standings')) {
            this.renditjaTot = res.standings.filter((x) => x.type === 'TOTAL');
          }
          if (TYPE === 'matches') {
            this.button1 = false;
          }
          if (TYPE === 'standings') {
            this.button2 = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    myMoment(date) {
      return this.$moment(date).format('LT');
    },
    javaFilter(javaThis, index) {
      const len = this.ndeshjet.length;
      const previous = this.ndeshjet[(index - 1) % len];
      if (previous === undefined) {
        if (javaThis.matchday === 1) {
          return 'JAVA 1-rë';
        }
        return null;
      }
      if (previous.matchday - javaThis.matchday === 0 || javaThis.matchday === null) {
        return null;
      }
      if (javaThis.matchday === 1) {
        return 'JAVA 1-rë';
      }
      if (javaThis.matchday === 14) {
        return 'JAVA 14-të';
      }
      if (javaThis.matchday.toString().endsWith('4')) {
        return `JAVA ${javaThis.matchday}-t`;
      }
      return `JAVA ${javaThis.matchday}-të`;
    },
    dataFilter(dataThis, index) {
      const len = this.ndeshjet.length;
      const previous = this.ndeshjet[(index - 1) % len];
      if (previous === undefined) {
        return this.$moment(dataThis.utcDate)
          .calendar({
            sameDay: '[SOT]',
            nextDay: '[NESËR]',
            nextWeek: 'dddd',
            lastDay: '[DJE]',
            lastWeek: 'LL',
            sameElse: 'LL',
          }).toUpperCase();
      }
      const dataTani = parseInt(this.$moment(dataThis.utcDate).format('DDMMYYYY'), 10);
      const dataPas = parseInt(this.$moment(previous.utcDate).format('DDMMYYYY'), 10);
      if (dataThis.utcDate === null || previous.utcDate === null) {
        return 'E papërcaktuar';
      }
      if (dataPas - dataTani === 0) {
        return null;
      }
      return this.$moment(dataThis.utcDate)
        .calendar({
          sameDay: '[SOT]',
          nextDay: '[NESËR]',
          nextWeek: 'dddd',
          lastDay: '[DJE]',
          lastWeek: 'LL',
          sameElse: 'LL',
        }).toUpperCase();
    },
    imageLoadOnError(e) {
      e.target.src = 'https://upload.wikimedia.org/wikipedia/commons/5/52/Coa_Illustration_Shield_Triangular.svg';
    },
  },
  computed: {
    ndeshjeTani() {
      const ndeshjaaktuale = this.ndeshjet.filter((n) => n.status === 'FINISHED');
      const i = ndeshjaaktuale.length;
      const ndeshjetPak = this.ndeshjet.slice(i - 12, i + 22);
      const ndeshjetFillim = this.ndeshjet.slice(0, i + 22);
      if (i - 12 < 0) {
        return ndeshjetFillim;
      }
      return ndeshjetPak;
    },
  },
};
