import Vue from 'vue';
import VueRouter from 'vue-router';
import Panoramasport from '../views/Panoramasport.vue';
import Kompeticion from '../views/Kompeticion.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: '',
    component: Panoramasport,
    children: [
      {
        path: ':id-:name',
        name: 'Kompeticion',
        components: { Kompeticion },
      },
    ],
  },
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});

export default router;
